@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
.left_side {
  flex-basis: 50%;
}

.left_side .tittle {
  font-weight: 700;
  font-size: 70px;
  font-family: "Montserrat";
  text-transform: uppercase;
  color: var(--headingcolor);
  cursor: pointer;
}

.left_side .hero_disc {
  padding: 15px 0;
  font-size: 15px;
  color: var(--headingcolor);
}
.fancyHero {
  font-weight: 700;
  font-family: var(--headingfamily);
  font-size: 18px;
  color: var(--button);
}
.btn_group > .btn {
  outline: 0;
  border: 0;
  background-color: var(--button);
  cursor: pointer;
  padding: 7px 14px;
  display: block;
  font-size: 15px;
  position: relative;
  overflow: hidden;
  z-index: 0;
  border-radius: 10px;
}
.leftbtn {
  width: 100%;
  color: var(--btntext);
}
/* ---------------Quotes styling---------------- */
.fa-quote-left {
  color: orange;
  font-size: 20px;
  margin-right: 5px;
}
.fa-quote-right {
  color: orange;
  font-size: 20px;
  margin-left: 5px;
}
@media (max-width: 768px) {
  /*responsive for First section */

  .left_side {
    flex-basis: 100%;
  }

  .left_side .tittle {
    font-size: 40px;
  }
}

/*Extra small Devices*/
@media (max-width: 500px) {
  .btn_group button {
    width: 100%;
  }
}

/*Extra Large Devices*/
@media (min-width: 1800px) {
  .left_side {
    flex-basis: 100%;
  }
}

.right_side {
  flex-basis: 50%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
/*right side Images */
.imgrightCon {
  height: 100%;
  display: flex;
}
.imgrightCon div {
  margin: 0 15px;
}
.imgrightCon .col1 {
  margin-top: 90px;
  display: flex;
  align-items: flex-start;
}
.imgrightCon .col2,
.col3 {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.col3 {
  margin-top: 20px !important;
  align-items: flex-end !important;
}

.col2 img,
.col3 img {
  margin: 10px 0;
}
.imgrightCon img {
  object-fit: cover;
}

/* ------------------------responsive code starts for all the index page--------------------- */
@media (max-width: 768px) {
  /*responsive for First section */

  .right_side {
    justify-content: center;
    flex-basis: 100%;
  }

  .right_side {
    padding-left: 0px;
    margin-top: 15px;
  }
  .col3 {
    display: none;
  }
  /*responsive for second section */
}

/*Extra Large Devices*/
@media (min-width: 1800px) {
  .right_side {
    flex-basis: 100%;
  }
}

.hero_content {
  /*padding: 70px 100px 70px 100px;*/
  width: 85%;
  margin: 70px auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  border-bottom-left-radius: 153px;
  border-bottom-right-radius: 153px;
}
@media (max-width: 768px) {
  /*responsive for First section */
  .hero_content {
    width: 100%;
    border-radius: 0;
    width: 90%;
    margin: 10px auto;
    overflow: hidden;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #fff;
}
nav {
  border-bottom: 1px solid var(--cardborder);
  display: flex;
  height: 50px;
  width: 100%;
  align-items: center;
  justify-content: space-around;
  text-transform: capitalize;
  background: transparent !important;
  z-index: 999999;
  padding: 30px;
  color: var(--navtext);
}
nav a,
.logo {
  color: var(--text-color);
  font-weight: 400;
  font-family: "Montserrat";
  color: var(--navtext);
}

.logo .for_mobile {
  display: none;
}
.mainHeadingNav {
  font-size: 18px;
}
.mainHeadingNav p span {
  font-weight: 700;
}
nav ul {
  display: flex;
  justify-content: space-around;
  width: 40%;
}
ul li {
  cursor: pointer;
  color: var(--navtext);
}
nav li a .icons {
  color: #fff;
  display: none;
  font-size: 20px;
}
li a span {
  transition: 0.2s ease;
  color: var(--navtext);
}
li a span:hover {
  color: var(--button);
}

nav ul li p {
  color: #fff;
  font-size: 10px;
  margin-top: 5px;
  display: none;
  letter-spacing: 0.5px;
  margin-right: 3px;
}

/* ----------- design for Dropdown  search ------------ */
.dropdown_data {
  z-index: 100;
  position: absolute;
  top: 45px;
  right: 100px;
  display: flex;
  flex-direction: column;
  width: 250px;
  background-color: #343a40;
  font-family: "poppins", sans-serif;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
  transition: 0.2s ease-in-out;
  cursor: pointer;
  padding: 5px;
  border-radius: 5px;
}

/* ---------after for the triangle------------- */

.li {
  font-size: 12px;
  padding: 15px 10px;
  border-radius: 5px;
  display: block;
}
.drp_down_holder {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.dropdown_data .li:hover {
  background-color: var(--feelhover);
}

.user__login {
  display: flex;
}
.user__login .li {
  margin: 0 5px;
}
.logedin_data {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  border-radius: 20px;
}
/*//notif*/
.notif_btn {
  margin-left: 20px;
}
.notif_btn i {
  font-size: 18px;
  color: var(--headingcolor);
  /*background: var(--button);*/
  padding: 8px;
  border-radius: 50%;
}
.notif_len {
  font-size: 60px;
  color: var(--headingcolor);
}

/* ------------ responsive code for nav bar starts ------------- */
@media (max-width: 1200px) {
  .dropdown_data {
    right: 60px;
  }
}
@media (max-width: 768px) {
  .mainHeadingNav {
    color: #fff !important;
  }

  .dropdown_data {
    top: 36px;
  }
  nav {
    padding: 0px;
  }
  .special {
    display: none !important;
  }
  .for_desktop {
    display: none;
  }
  .for_mobile {
    display: block !important;
    padding-right: 20px;
  }
  .for_mobile i {
    color: #fff;
  }
  .special_links {
    display: block !important;
  }
  .fa-user-circle {
    font-size: 30px !important;
  }
  nav .logo {
    width: 100%;
    height: 50px;
    background-color: #343a40;
    color: #fff !important;
    padding-left: 20px;
    position: fixed;
    font-size: 15px;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    font-size: 25px;
    z-index: 9999;
    display: flex;
    justify-content: space-between;
  }

  nav ul {
    position: fixed;
    bottom: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
    background-color: #343a40;

    height: 50px;
    display: flex;
    align-items: center;
    padding: 5px 0;
    z-index: 999;
  }
  nav li a .icons {
    color: #fff;
    display: block;
  }
  nav li a:hover .icons {
    color: var(--button);
  }
  a span {
    display: none;
  }
  section h2 {
    padding: 0 10px;
  }

  nav .indi {
    display: block;
  }

  .dropdown_data {
    right: 20px;
  }
  nav .logo {
    color: #fff !important;
  }
}
